  }

  &_2 {

    &:before {
      background: #6ed1cd;
    }

  }

  &_3 {

    &:before {
      background: #e0e6ee;
    }

    #{$g}__title {
      color: #3b3b3d;
    }

    #{$g}__description {
      color: #3b3b3d;
    }

  }

  &_4 {

    &:before {
      background: #8033e0;
    }

  }

  &__text-wrap {
    display: flex;
    align-items: center;
  }

  &__text-box {
    padding-right: 40px;
  }

  &__title {
    margin: 0;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 36px;
    color: #fff;
    line-height: 48px;
