    letter-spacing: .2px;
  }

  &__description {
    margin: 30px 0;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 14px;
    color: #fff;
    line-height: 24px;
    letter-spacing: .2px;
  }

  &__form-wrap {
    padding: 100px 0 0 100px;
  }

}

@media only screen and (max-width: 1024px) {

  .page-service-list {
    margin: 40px -15px 0;
  }

