  .page-service-list {
    justify-content: center;
    margin: 20px 0 0;

    $g: &;

    &_2 {

      #{$g}__link {
        border-color: #3b3b3d;

        &:hover {
          background: #3b3b3d;
        }

      }

    }

    &_3 {

      #{$g}__link {
        border-color: #8033e0;

        &:hover {
          background: #8033e0;
        }

      }

    }

    &__item {
      width: 100%;
      padding: 0;
      max-width: 100%;
    }

    &__link {
      min-height: auto;
      padding: 25px;

      & h3 {
        margin: 0;
        font-size: 14px;
        line-height: 24px;
      }

      & ul {
        margin: 20px 0 0 0;
        padding: 0;
        list-style: none;

        & li {
          font-family: $font-family-one;
          font-weight: 300;
          font-size: 14px;
          color: #272727;
          line-height: 24px;
          letter-spacing: .2px;
          transition: all .2s ease-in-out;
        }
      }

    }

  }

  .page-important {
    padding: 25px;
    background: #e0e6ee;

    $g: &;

    &_2 {
      background: #3b3b3d !important;
    }

    &_3 {
      background: #3b3b3d;

      #{$g}__title {
        color: #fff;
      }

      #{$g}__description {
        color: #fff;
      }

    }

    &_4 {
      background: #6ed1cd;

      #{$g}__title {
        color: #3b3b3d;
      }

      #{$g}__description {
        color: #3b3b3d;
      }

    }

    &__text-box {
      padding-right: 0;

      &_pl {
        padding-left: 0;
      }

    }

    &__title {
      padding-bottom: 0;
      font-size: 16px;
      line-height: 24px;
    }

    &__description {
      margin: 20px 0;
    }

  }

  .page-important-two {
    padding-bottom: 0;

    &:before {
      content: none;
    }

    &__text-wrap {
      display: flex;
      align-items: center;
      padding: 25px;
      background: #3b3b3d;

      &_1 {
        background: #3b3b3d;
      }

      &_2 {
        background: #6ed1cd;
      }

      &_3 {
        background: #e0e6ee;
      }

      &_4 {
        background: #8033e0;
      }

    }

    &__text-box {
      padding-right: 0;
    }

    &__title {
      font-size: 16px;
      line-height: 24px;
    }

    &__description {
      margin: 20px 0;
      font-size: 14px;
      line-height: 24px;
    }

    &__form-wrap {
      padding: 25px;
    }

  }

}

