.content-b {
  position: relative;
  width: 100%;
  height: 100vh;
  outline: 0;

  &_2 {
    background: #6ed1cd;
  }

  &_3 {
    background: #e0e6ee;
  }

  &_4 {
    background: #8033e0;
  }

  &__container {
    clip: rect(auto, auto, auto, auto);
    position: absolute;
    width: 100%;
    min-height: 100vh;
    overflow: hidden;
  }

  &__wrap {
    width: 100%;

    &_fix {
      position: fixed;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100vh;
    }

  }

  &__box {

  }

  &__title {
    display: inline-block;
    margin: 0;
    position: relative;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 208px;
    text-transform: uppercase;
    letter-spacing: .2px;
    background-image: url("../images/cm/cm1.png");
    background-repeat: no-repeat;
    background-color: #3b3b3d;
    background-size: 100% 100%;
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;

    &_2 {
      background-image: url("../images/cm/cm2.png");
    }

    &_3 {
      background-image: url("../images/cm/cm3.png");
    }

    &_4 {
      background-image: url("../images/cm/cm4.png");
    }

  }

  &__description {
    margin: 0;
    min-height: 82px;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 36px;
    color: #6ed1cd;

    &_2 {
      color: #fff;
    }

    &_3 {
      color: #8033e0;
    }

    &_4 {
      color: #6ed1cd;
    }

  }

  &__description-sub {
    margin-top: 50px;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 16px;
    color: #3b3b3d;
  }

  &__link-line {
    position: relative;
    margin-top: 25px;
    display: inline-block;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 16px;
    color: #3b3b3d;
    text-transform: uppercase;
    text-decoration: none;

    & span {
      position: absolute;
      top: 8px;
      right: -410px;
      width: 400px;
      height: 2px;
      background: #6ed1cd;

      &:before {
        content: '';
        position: absolute;
        top: -9px;
        right: 14px;
        width: 2px;
        height: 12px;
        background: #6ed1cd;
        transform: rotate(-45deg);
      }

      &:after {
        content: '';
        position: absolute;
        top: -9px;
        right: 6px;
        width: 2px;
        height: 12px;
        background: #6ed1cd;
        transform: rotate(-45deg);
      }

    }

    &_2 span {
      background: #fff !important;

      &:before {
        background: #fff !important;
      }

      &:after {
        background: #fff !important;
      }

    }

    &_3 span {
      background: #8033e0 !important;

      &:before {
        background: #8033e0 !important;
      }

      &:after {
        background: #8033e0 !important;
      }

    }

    &_3 span {
      background: #6ed1cd !important;

      &:before {
        background: #6ed1cd !important;
      }

      &:after {
        background: #6ed1cd !important;
      }

    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .content-b {

    &__title {
      font-size: 157px;
    }

    &__description {
      margin-top: 15px;
      min-height: 82px;
      font-size: 36px;
      word-wrap: break-word;
    }

    &__description-sub {
      margin-top: 110px;
      font-size: 30px;
    }

    &__link-line {
      margin-top: 25px;
      font-size: 34px;

      & span {
        top: 29px;
        right: -363px;
        width: 333px;
      }

    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .content-b {

    &__title {
      font-size: 60px;
    }

    &__description {
      margin-top: 10px;
      min-height: 45px;
      font-size: 18px;
      word-wrap: break-word;
    }

    &__description-sub {
      margin-top: 55px;
      font-size: 14px;
    }

    &__link-line {
      margin-top: 10px;
      font-size: 16px;

      & span {
        top: 13px;
        right: -183px;
        width: 155px;
        height: 1px;

        &:before {
          top: -9px;
          width: 1px;
          height: 10px;
        }

        &:after {
          top: -9px;
          right: 6px;
          width: 1px;
          height: 10px;
        }

      }

    }

  }

}

