.scroll-wrap {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  //display: flex;
  align-items: center;
}

.scroll-list {
  padding: 0;
  list-style: none;

  $g: &;

  &_2 {
    #{$g}__item {
      &_active {
        &:before {
          background: #fff;
        }
      }
    }
  }

  &_3 {
    #{$g}__item {
      &_active {
        &:before {
          background: #8033e0;
        }
      }
    }
  }

  &__item {
    position: relative;
    padding-left: 30px;
    margin-bottom: 31px;

    &:last-child {
      margin-bottom: 0;
    }

    &_active {

      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 8px;
        width: 21px;
        height: 2px;
        background: #6ed1cd;
      }

    }

  }

  &__link {
    display: inline-block;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 16px;
    color: #3b3b3d;
    text-transform: uppercase;
    text-decoration: none;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .scroll-wrap {
    display: none;
  }

}

